@use "@/styles/vars";
@use "@/styles/color-themes";
@use "@/styles/utils";
@use "@/styles/responsive-breakpoints"as media;

.friendslist {
    width: 300px;
    border-radius: 1em;

    &__ctn {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 0.6em;

        &__title {
            text-transform: uppercase;
            color: vars.$gold;
            text-align: center;
            font-size: 20px;
        }

        &__img {
            width: 95%;
        }

        &__text {
            text-align: center;
            font-size: 15px;
            margin-top: 1em;
        }
    }
}


// Style when on tablet and bigger
@include media.responsive("desktop") {
    .friendslist {
        width: 30em;

        &__ctn {
            &__img {
                width: 65%;
            }

            &__text {
                font-size: 18px;
            }
        }
    }
}